
.note-list {
  margin-top: 20px;
}
.note-list li {
  padding: 10px 0;
}
.note-list li+li {
  border-top: 1px solid #e2e2e2;
}
.note-ls-author-avatar {
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  font-size: 18px;
  display: inline-block;
  border-radius: 50%;
  border: 1px solid #ddd;
}
.note-ls-author {
  line-height: 40px;
  display: inline-block;
  margin-bottom: 5px;
}
.note-ls-author small {
  color: #999;
}
.note-ls-tit {
}



